import Vue from 'vue';
import VueRouter from 'vue-router';
import Home from '../views/Home.vue';
import AddEntity from '../views/AddEntity.vue'
import UploaderPage from '../views/UploaderPage.vue';
import UploadedFilesPage from '../views/UploadedFilesPage';
import AdministratorPage from '../views/AdministratorPage';
import PrivateArea from '../views/PrivateArea';

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/addentity',
    name: 'AddEntity',
    component: AddEntity
  },
  {
    path: '/uploaders',
    name: 'UploaderPage',
    component: UploaderPage
  },
  {
    path: '/administrators',
    name: 'AdministratorPage',
    component: AdministratorPage
  },
  {
    path: '/uploadedfiles',
    name: 'UploadedFilesPage',
    component: UploadedFilesPage,
  },
  {
    path: '/privatearea',
    name: 'PrivateArea',
    component: PrivateArea
  }
]

const router = new VueRouter({
  routes
})

export default router
