<template>
  <main>
    <h1>All Uploaded Files</h1>
    <p>
      In the table below are reported all the uploaders and the files they have
      uploaded. You can filter them selecting the two dates below. By default,
      the period of time is the last month. To see the files of another
      uploader, just click on his username.
      <em v-if="this.particularInfo.length === 0">
        No file has been uploaded yet.</em
      >
    </p>
    <div>
      <label for="from-day">
        From:
        <input
          type="date"
          min="1970-01-01"
          id="from-day"
          v-model="from"
          @change="changeCurrentFiles()"
        />
      </label>
      <label for="to-day">
        To:
        <input
          type="date"
          min="1970-01-01"
          id="to-day"
          v-model="to"
          @change="changeCurrentFiles()"
        />
      </label>
    </div>
    <section>
      <table>
        <thead>
          <tr>
            <th>Uploader</th>
            <th>Total Files</th>
          </tr>
        </thead>
        <tbody v-if="particularInfo.length !== 0">
          <tr v-for="(info, index) in particularInfo" :key="info.username">
            <td @click="[setActive(index + 1), changeCurrentFiles()]">
              {{ info.username }}
            </td>
            <td>{{ info.uploadedFiles.length }}</td>
          </tr>
        </tbody>
        <tbody
          v-else
          style="
            display: flex;
            flex-flow: row nowrap;
            justify-content: center;
            align-items: center;
          "
        >
          <em> there are no uploaders yet </em>
        </tbody>
      </table>
      <UploadedFilesTable v-bind:files="currentFiles" />
    </section>
  </main>
</template>

<script>
import Axios from "axios";
import UploadedFilesTable from "@/components/UploadedFilesTable.vue";

export default {
  name: "UploadedFilePage",
  components: {
    UploadedFilesTable,
  },
  data: function () {
    return {
      particularInfo: [],
      currentFiles: [],
      currentUploader: "",
      from: "",
      to: "",
    };
  },
  created: function () {
    Axios.get(`${process.env.VUE_APP_APIROOT}administratorarea/particularinfo`)
      .then((response) => {
        if (response.data.length !== 0) {
          this.particularInfo = response.data;
          this.currentFiles = this.particularInfo[0].uploadedFiles;
          this.currentUploader = this.particularInfo[0].username;
          //apply calendar filter
          this.changeCurrentFiles();
        }
      })
      .catch((error) => console.log(error));

    //set calendar
    let today = new Date();
    this.from = `${today.getFullYear()}-${
      today.getMonth() > 9 ? today.getMonth() : "0".concat(today.getMonth())
    }-01`;
    this.to = `${today.getFullYear()}-${
      today.getMonth() + 1 > 9
        ? today.getMonth() + 1
        : "0".concat(today.getMonth() + 1)
    }-01`;
  },
  methods: {
    changeCurrentFiles: function () {
      this.currentFiles = this.particularInfo
        .filter((uploader) => uploader.username === this.currentUploader)[0]
        .uploadedFiles.filter((file) => {
          if (this.from !== "" && this.to !== "") {
            let fromDate = new Date(this.from);
            let toDate = new Date(this.to);
            let uploadDate = new Date(file.upload);
            return uploadDate > fromDate && uploadDate < toDate;
          } else {
            return true;
          }
        });
    },
    setActive: function (index) {
      for (let i = 1; i <= this.particularInfo.length; i++) {
        if (i !== index) {
          event.target.parentElement.parentElement.querySelector(
            `tr:nth-of-type(${i}) td:first-child`
          ).className = "inactive";
        } else {
          event.target.parentElement.parentElement.querySelector(
            `tr:nth-of-type(${i}) td:first-child`
          ).className = "active";
          this.currentUploader = event.target.parentElement.parentElement.querySelector(
            `tr:nth-of-type(${i}) td:first-child`
          ).innerText;
        }
      }
    },
  },
};
</script>

<style scoped>
p {
  width: 95%;
  margin-right: auto;
  margin-left: auto;
}

p em {
  font-style: normal;
  font-weight: bold;
}

div {
  display: flex;
  flex-flow: row nowrap;
  justify-content: flex-end;
  margin-right: auto;
  margin-left: auto;
  width: 80%;
  font-family: "Nunito";
}

div label:first-child {
  margin-right: 1vw;
}

div input[type="date"] {
  border: 2px solid rgba(0, 0, 0, 0.83);
  border-radius: 4px;
}

section {
  margin-top: 1vh;
  display: flex;
  flex-flow: row nowrap;
  width: 80%;
  margin-right: auto;
  margin-left: auto;
  border: 4px solid black;
  height: 50vh;
  border-top-left-radius: 30px;
  border-bottom-left-radius: 30px;
}

section > table:first-of-type {
  flex-basis: 20%;
  display: flex;
  flex-flow: column nowrap;
  height: 100%;
  border-right: 2px solid grey;
  border-top-left-radius: 30px;
}

section table:first-of-type thead {
  flex-basis: 15%;
  display: flex;
  flex-flow: row nowrap;
  background-color: rgba(0, 0, 0, 0.83);
  color: rgb(214, 139, 0);
  border-top-left-radius: 20px;
}

section table:first-of-type thead tr {
  display: flex;
  flex-basis: 100%;
  flex-flow: row nowrap;
  height: 100%;
  align-items: center;
}

section table:first-of-type thead tr th {
  flex: 50%;
}

section table:first-of-type tbody {
  display: flex;
  flex-basis: 85%;
  flex-flow: column nowrap;
  max-height: 85%;
  overflow-y: auto;
}

section table:first-of-type tbody tr {
  width: 100%;
  flex-basis: 5%;
  display: flex;
  flex-flow: row nowrap;
  min-height: 25px;
}

section table:first-of-type tbody tr td {
  flex-basis: 50%;
  text-align: center;
}

section table:first-of-type tbody tr td:first-child:hover {
  cursor: pointer;
  text-decoration: underline;
  color: orange;
}

.active {
  color: orange;
  text-decoration: underline;
}

.inactive {
  color: black;
  text-decoration: none;
}
</style>