<template>
  <table>
    <thead>
      <tr>
        <th>Username</th>
        <th>Name &amp; Surname</th>
        <th>Password</th>
        <th>Edit</th>
        <th>Save</th>
        <th>Delete</th>
      </tr>
    </thead>
    <tbody>
      <tr v-for="(administrator, index) in administrators" :key="index">
        <td>{{ administrator.username }}</td>
        <td>
          <input
            type="text"
            v-model="administrator.nameSurname"
            class="inactive"
            readonly
          />
        </td>
        <td>
          <input
            type="password"
            v-model="administrator.password"
            class="inactive"
            readonly
          />
        </td>
        <td>
          <button @click="edit(administrator, index)">
            <img src="closed_lock.png" alt="closed_lock" />
          </button>
        </td>
        <td>
          <button @click="commitChanges(administrator)">
            <img src="commit_changes.png" alt="save_in_database" />
          </button>
        </td>
        <td>
          <button @click="deleteAdministrator(administrator.username)">
            <img src="red_bin.png" alt="red_bin" />
          </button>
        </td>
      </tr>
    </tbody>
  </table>
</template>

<script>
import Axios from "axios";

export default {
  name: "AdministratorTable",
  props: ["administrators"],
  methods: {
    commitChanges: function (administrator) {
      Axios.put(
        `${process.env.VUE_APP_APIROOT}administratorarea/administrator`,
        administrator
      )
        .then((response) => console.log(response.status))
        .catch((error) => console.log(error));
    },
    deleteAdministrator: function (username) {
      Axios.delete(
        `${process.env.VUE_APP_APIROOT}administratorarea/administrator/${username}`
      )
        .then((response) => {
          console.log(response.data);
          this.$emit("delete-administrator", username);
        })
        .catch((error) => console.log(error));
    },
    edit: function (administrator, index) {
      if (event.target.src.includes("closed_lock")) {
        event.target.src = "opened_lock.png";
        document.querySelector(`table tbody tr:nth-of-type(${index + 1}) td input[type='text']`).readOnly = false;
        document.querySelector(`table tbody tr:nth-of-type(${index + 1}) td input[type='password']`).readOnly = false;
        document.querySelector(`table tbody tr:nth-of-type(${index + 1}) td input[type='text']`).className = "active";
        document.querySelector(`table tbody tr:nth-of-type(${index + 1}) td input[type='password']`).className = "active";
        document.querySelector(`table tbody tr:nth-of-type(${index + 1}) td input[type='password']`).type = "text";
      } else {
        event.target.src = "closed_lock.png";
        document.querySelector(`table tbody tr:nth-of-type(${index + 1}) td:nth-of-type(2) input`).readOnly = true;
        document.querySelector(`table tbody tr:nth-of-type(${index + 1}) td:nth-of-type(3) input`).readOnly = true;
        document.querySelector(`table tbody tr:nth-of-type(${index + 1}) td:nth-of-type(2) input`).className = "inactive";
        document.querySelector(`table tbody tr:nth-of-type(${index + 1}) td:nth-of-type(3) input`).className = "inactive";
        document.querySelector(`table tbody tr:nth-of-type(${index + 1}) td:nth-of-type(3) input`).type = 'password';
      }
    },
  },
};
</script>

<style scoped>
table {
  margin-right: auto;
  margin-left: auto;
  width: 95%;
  text-align: center;
  border: 2px solid grey;
  display: flex;
  flex-flow: column nowrap;
}

table,
thead,
thead tr {
  border-top-left-radius: 20px;
  border-top-right-radius: 20px;
}

table,
tbody,
tbody tr:last-child {
  border-bottom-left-radius: 6px;
  border-bottom-right-radius: 6px;
}

table thead {
  display: flex;
  flex-flow: row nowrap;
}

table thead tr {
  flex-basis: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 8vh;
  background-color: rgb(36, 36, 36);
}

table thead tr th:nth-of-type(1) {
  flex-basis: 25%;
  flex-shrink: 0;
  flex-grow: 1;
  padding: 0;
  color: white;
  font-size: 3vh;
  height: 4.5vh;
}

table thead tr th:nth-of-type(2),
table thead tr th:nth-of-type(3) {
  flex-basis: 20%;
  flex-shrink: 0;
  flex-grow: 1;
  padding: 0;
  color: white;
  font-size: 3vh;
  height: 4.5vh;
}

table thead tr th:nth-of-type(4),
table thead tr th:nth-of-type(5),
table thead tr th:nth-of-type(6) {
  flex-basis: 10%;
  flex-shrink: 0;
  flex-grow: 1;
  padding: 0;
  color: white;
  font-size: 3vh;
  height: 4.5vh;
}

table tbody {
  display: flex;
  flex-flow: column nowrap;
  max-height: 50vh;
  overflow-y: auto;
}

table tbody tr:nth-child(odd) {
  background-color: rgb(230, 230, 230);
}

table tbody tr {
  flex-basis: 100%;
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
  height: 8vh;
}

table tbody tr td:nth-of-type(1) {
  flex-basis: 25%;
  flex-shrink: 0;
  flex-grow: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 6vh;
}

table tbody tr td:nth-of-type(2),
table tbody tr td:nth-of-type(3) {
  flex-basis: 20%;
  flex-shrink: 0;
  flex-grow: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 6vh;
}

table tbody tr td:nth-of-type(4),
table tbody tr td:nth-of-type(5),
table tbody tr td:nth-of-type(6) {
  flex-basis: 10%;
  flex-shrink: 0;
  flex-grow: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 6vh;
}

table tbody tr td input {
  text-align: center;
}

table tbody tr td button {
  height: 90%;
  padding: 0;
  margin: 0;
  display: block;
  flex-basis: 30%;
  border: none;
  background-color: inherit;
}

table tbody tr td button:hover {
  cursor: pointer;
}

table tbody tr td button:focus {
  outline: none;
}

table tbody tr td button img {
  height: 80%;
}

table tbody tr td > img {
  height: 80%;
  border: 1px solid black;
}

table tbody tr td span {
  font-style: italic;
}

.active {
  background-color: white;
  border: 2px solid black;
  border-radius: 5px;
}

.inactive {
  background-color: inherit;
  border: none;
}
</style>