<template>
  <table>
    <thead>
      <tr>
        <th>Username</th>
        <th>Email</th>
        <th>Name, Surname</th>
        <th>Logo</th>
        <th>Password</th>
        <th>Edit</th>
        <th>Save</th>
        <th>Delete</th>
      </tr>
    </thead>
    <tbody v-if="uploaders.length !== 0">
      <tr v-for="(uploader, index) in uploaders" :key="index">
        <td>{{ uploader.username }}</td>
        <td>
          <input
            type="email"
            v-model="uploader.email"
            class="inactive"
            readonly
          />
        </td>
        <td>
          <input
            type="text"
            v-model="uploader.nameSurname"
            class="inactive"
            readonly
          />
        </td>
        <td>
          <img
            v-if="uploader.logo != ''"
            :src="'data:image/*;base64,'.concat(uploader.logo)"
            alt="uploader_logo"
          />
          <img v-else src="default_profile_picture.png" />
          <label :for="'profile_picture'.concat(index)" class="active">
            change logo
            <input
              type="file"
              :id="'profile_picture'.concat(index)"
              @change="changeImage(index)"
            />
          </label>
        </td>
        <td>
          <input
            type="password"
            v-model="uploader.password"
            class="inactive"
            readonly
          />
        </td>
        <td>
          <button>
            <img
              src="closed_lock.png"
              alt="closed_lock"
              @click="edit(uploader, index)"
            />
          </button>
        </td>
        <td>
          <button @click="commitChanges(uploader)">
            <img src="commit_changes.png" alt="save_in_database" />
          </button>
        </td>
        <td>
          <button @click="deleteUploader(uploader.username)">
            <img src="red_bin.png" alt="red_bin" />
          </button>
        </td>
      </tr>
    </tbody>
    <tbody v-else style="margin-top: 5vh; margin-bottom: 5vh;">
      <em> There are no uploaders yet</em>
    </tbody>
  </table>
</template>

<script>
import Axios from "axios";

export default {
  name: "UploaderTable",
  data: function () {
    return {
      uploaders: [],
    };
  },
  created: function () {
    Axios.get(`${process.env.VUE_APP_APIROOT}administratorarea/uploaders`)
      .then((response) => {
        this.uploaders = response.data;
      })
      .catch((error) => console.log(error));
  },
  methods: {
    commitChanges: function (uploader) {
      Axios.put(
        `${process.env.VUE_APP_APIROOT}administratorarea/uploader`,
        uploader
      )
        .then(() => alert("changes committed!"))
        .catch((error) => console.log(error));
    },
    changeImage: function (index) {
      let imageFile = event.target.files[0];
      let fileReader = new FileReader();
      let imageElement = event.target.parentElement.parentElement.querySelector(
        "img"
      );
      fileReader.readAsDataURL(imageFile);
      let that = this;
      fileReader.onload = function () {
        imageElement.src = fileReader.result;
        that.uploaders[index].logo = fileReader.result.substring(23);
      };
    },
    deleteUploader: function (username) {
      Axios.delete(
        `${process.env.VUE_APP_APIROOT}administratorarea/uploader/${username}`
      )
        .then((response) => {
          console.log(response.data);
          this.uploaders = this.uploaders.filter(
            (uploader) => uploader.username !== username
          );
        })
        .catch((error) => console.log(error));
    },
    edit: function (uploader, index) {
      if (event.target.src.includes("closed_lock")) {
        event.target.src = "opened_lock.png";
        document.querySelector(
          `table tbody tr:nth-of-type(${index + 1}) td input[type='email']`
        ).readOnly = false;
        document.querySelector(
          `table tbody tr:nth-of-type(${index + 1}) td input[type='email']`
        ).className = "active";
        document.querySelector(
          `table tbody tr:nth-of-type(${index + 1}) td input[type='text']`
        ).readOnly = false;
        document.querySelector(
          `table tbody tr:nth-of-type(${index + 1}) td input[type='text']`
        ).className = "active";
        document.querySelector(
          `table tbody tr:nth-of-type(${index + 1}) td input[type='password']`
        ).readOnly = false;
        document.querySelector(
          `table tbody tr:nth-of-type(${index + 1}) td input[type='password']`
        ).className = "active";
        document.querySelector(
          `table tbody tr:nth-of-type(${index + 1}) td input[type='password']`
        ).type = "text";
        document.querySelector(
          `table tbody tr:nth-of-type(${index + 1}) td label`
        ).style.display = "block";
        document.querySelector(
          `table tbody tr:nth-of-type(${index + 1}) td img`
        ).style.display = "none";
      } else {
        event.target.src = "closed_lock.png";
        document.querySelector(
          `table tbody tr:nth-of-type(${
            index + 1
          }) td:nth-of-type(5) input[type='text']`
        ).type = "password";
        document.querySelector(
          `table tbody tr:nth-of-type(${index + 1}) td input[type='email']`
        ).readOnly = true;
        document.querySelector(
          `table tbody tr:nth-of-type(${index + 1}) td input[type='email']`
        ).className = "inactive";
        document.querySelector(
          `table tbody tr:nth-of-type(${index + 1}) td input[type='text']`
        ).readOnly = true;
        document.querySelector(
          `table tbody tr:nth-of-type(${index + 1}) td input[type='text']`
        ).className = "inactive";
        document.querySelector(
          `table tbody tr:nth-of-type(${index + 1}) td input[type='password']`
        ).readOnly = true;
        document.querySelector(
          `table tbody tr:nth-of-type(${index + 1}) td input[type='password']`
        ).className = "inactive";
        document.querySelector(
          `table tbody tr:nth-of-type(${index + 1}) td label`
        ).style.display = "none";
        document.querySelector(
          `table tbody tr:nth-of-type(${index + 1}) td img`
        ).style.display = "inline-block";
      }
    },
  },
};
</script>

<style scoped>
table {
  margin-right: auto;
  margin-left: auto;
  width: 95%;
  text-align: center;
  border: 2px solid grey;
  display: flex;
  flex-flow: column nowrap;
}

table thead {
  display: flex;
  flex-flow: row nowrap;
}

table thead tr {
  flex-basis: 100%;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  height: 8vh;
  background-color: rgb(36, 36, 36);
}

table thead tr th {
  flex: 1 1 14%;
  padding: 0;
  color: white;
  font-size: 3vh;
  height: 4.5vh;
}

table tbody {
  display: flex;
  flex-flow: column nowrap;
  max-height: 50vh;
  overflow-y: auto;
}

table tbody tr:nth-child(odd) {
  background-color: rgb(230, 230, 230);
}

table tbody tr {
  flex-basis: 100%;
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
  height: 8vh;
}

table tbody tr td {
  flex: 1 1 12.5%;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 6vh;
}

table tbody tr td label {
  display: none;
  text-decoration: underline;
  color: blue;
  width: 100%;
  height: 3.3vh;
  font-size: 2.4vh;
}

table tbody tr td label input[type="file"] {
  display: none;
}

table,
thead,
thead tr {
  border-top-left-radius: 20px;
  border-top-right-radius: 20px;
}

table,
tbody,
tbody tr:last-child {
  border-bottom-left-radius: 6px;
  border-bottom-right-radius: 6px;
}

table tbody tr td input {
  text-align: center;
  height: 3vh;
}

table tbody tr td button {
  height: 90%;
  padding: 0;
  margin: 0;
  display: block;
  flex-basis: 30%;
  border: none;
  background-color: inherit;
}

table tbody tr td button:hover,
table tbody tr td label:hover {
  cursor: pointer;
}

table tbody tr td button:focus {
  outline: none;
}

table tbody tr td button img {
  height: 80%;
}

table tbody tr td > img {
  height: 80%;
  border: 1px solid black;
  border-radius: 50%;
  box-sizing: border-box;
}

table tbody tr td span {
  font-style: italic;
}

.active {
  background-color: white;
  border: 2px solid black;
  border-radius: 5px;
}

.inactive {
  background-color: inherit;
  border: none;
}
</style>