<template>
  <main class="home">
    <h1>Administrator Realm</h1>
    <p>Dear administrator, these are the general info about the entire honeycomb storage system.</p>
    <table>
      <tr>
        <th>Total Administrators</th>
        <td>{{info.totalAdministrators}}</td>
      </tr>
      <tr>
        <th>Total Uploaders</th>
        <td>{{info.totalUploaders}}</td>
      </tr>
      <tr>
        <th>Total Consumers</th>
        <td>{{info.totalConsumers}}</td>
      </tr>
      <tr>
        <th>Total Uploaded Files</th>
        <td>{{info.totalUploadedFiles}}</td>
      </tr>
    </table>
  </main>
</template>

<script>
import Axios from "axios";
export default {
  name: "Home",
  data: function () {
    return {
      info: {},
    };
  },
  created: function () {
    Axios.get(`${process.env.VUE_APP_APIROOT}administratorarea/generalinfo`)
      .then((response) => (this.info = response.data))
      .catch((error) => console.log(error));
  },
};
</script>

