<template>
  <main>
    <h1>Private Area</h1>
    <p>
      Dear {{ administrator.username }}, welcome to your private area! To save
      changes, just click <span @click="confirmChanges()">here</span>.
    </p>
    <table>
      <tr>
        <th>Username</th>
        <td>{{ administrator.username }}</td>
      </tr>
      <tr>
        <th>Name, Surname</th>
        <td>
          <input
            type="text"
            class="inactive"
            v-model="administrator.nameSurname"
            @click="setWritable()"
            @keyup.enter="setReadOnly()"
            readonly
          />
        </td>
      </tr>
      <tr>
        <th>Password</th>
        <td>
          <input
            type="password"
            class="inactive"
            v-model="administrator.password"
            @click="setWritable()"
            @keyup.enter="setReadOnly()"
            readonly
          />
        </td>
      </tr>
    </table>
    <form :action="logoutLink()" method="post">
      <input type="submit" value="Logout" />
    </form>
  </main>
</template>

<script>
import Axios from "axios";

export default {
  name: "PrivateArea",
  data: function () {
    return {
      administrator: {},
    };
  },
  created: function () {
    Axios.get(`${process.env.VUE_APP_APIROOT}administratorarea/administrator`)
      .then((response) => (this.administrator = response.data))
      .catch((error) => console.log(error));
  },
  methods: {
    setWritable: function () {
      event.target.className = "active";
      event.target.readOnly = false;
      if (event.target.type === "password") {
        event.target.type = "text";
      }
    },
    setReadOnly: function () {
      event.target.className = "inactive";
      event.target.readOnly = true;
      if (event.target.value === this.administrator.password) {
        event.target.type = "password";
      }
    },
    logoutLink: function () {
      return `${process.env.VUE_APP_HOST}/logout`;
    },

    confirmChanges: function () {
      Axios.put(
        `${process.env.VUE_APP_APIROOT}administratorarea/administrator`,
        this.administrator
      )
        .then(() => alert("changes committed!"))
        .catch((error) => console.log(error));
    },
  },
};
</script>

<style scoped>
p{
  width: 95%;
  margin-right: auto;
  margin-left: auto;
}
p span {
  cursor: pointer;
  color: blue;
  text-decoration: underline;
}

form {
  margin: 1vh 30%;
  display: flex;
  flex-flow: row nowrap;
  justify-content: flex-end;
  width: 30%;
}

form input[type="submit"] {
  background-color: black;
  border-radius: 5px;
  padding: 1% 2%;
  width: 100px;
  font-family: "Russo One";
  font-size: 110%;
  color: rgb(214, 139, 0);
}

form input[type="submit"]:hover {
  cursor: pointer;
}

table {
  width: 40%;
  margin-left: auto;
  margin-right: auto;
}

table tr {
  height: 5vh;
}

table tr th {
  width: 35%;
  text-align: left;
}

table tr td {
  width: 65%;
  max-width: 400px;
}

table tr td input {
  box-sizing: border-box;
  width: 100%;
}

table tr td input:focus {
  outline: 0;
}

.active {
  border: 2px solid black;
  border-radius: 5px;
  background-color: white;
}

.inactive {
  border: 2px solid whitesmoke;
  background-color: inherit;
}
</style>