<template>
  <main>
    <h1>Manage all the Administrators</h1>
    <p>
      In this table are reported all the administrators of the platform. You can
      modify their information, except their username. You can also delete them,
      but be aware: this action <em>can not</em> be undone!
    </p>
    <AdministratorTable
      v-bind:administrators="administrators"
      @delete-administrator="deleteAdministrator"
    />
  </main>
</template>

<script>
import Axios from "axios";
import AdministratorTable from "@/components/AdministratorTable.vue";

export default {
  name: "AdministratorPage",
  components: {
    AdministratorTable,
  },
  data: function () {
    return {
      administrators: [],
    };
  },
  created: function () {
    Axios.get(`${process.env.VUE_APP_APIROOT}administratorarea/administrators`)
      .then((response) => (this.administrators = response.data))
      .catch((error) => console.log(error));
  },
  methods: {
    deleteAdministrator(username) {
      this.administrators = this.administrators.filter(
        (administrator) => administrator.username !== username
      );
    },
  },
};
</script>

<style scoped>
p{
  width: 95%;
  margin-left: auto;
  margin-right: auto;
}
em{
  font-style: normal;
  font-weight: bold;
}
</style>