<template>
  <main>
    <h1>Add new Entity</h1>
    <p>Here you can add a new administorator or a new uploader.</p>
    <form @submit="submitForm" method="POST">
      <label for="usernameField">Username:</label>
      <input
        type="text"
        id="usernameField"
        name="username"
        v-model="username"
        placeholder="enter username"
        required
      />
      <label for="emailField">Email:</label>
      <input
        type="email"
        id="emailField"
        name="email"
        v-model="email"
        placeholder="enter email"
        required
      />
      <label for="nameSurnameField">Name, Surname:</label>
      <input
        type="text"
        id="nameSurnameField"
        name="nameSurname"
        placeholder="enter name &amp; surname"
        required
      />
      <label for="passwordField">Password:</label>
      <input
        type="password"
        id="passwordField"
        name="password"
        placeholder="enter password"
        required
      />
      <span>Entity to create:</span>
      <label for="radioUploader">
        <input type="radio" id="radioUploader" value="uploader" v-model="entity" @click="checkUploaderPattern()"/>
        Uploader
      </label>
      <label for="radioAdministrator">
        <input type="radio" value="administrator" v-model="entity" id="radioAdministrator" @click="checkAdministratorPattern()"/>
        Administrator
      </label>
      <input type="submit" value="SUBMIT" />
    </form>
  </main>
</template>

<script>
export default {
  name: "AddEntity",
  data: function () {
    return {
      entity: "uploader",
      username: "",
      email: "",
    };
  },
  methods: {
    submitForm: function () {
      document.querySelector(
        "form"
      ).action = `${process.env.VUE_APP_APIROOT}administratorarea/${this.entity}`;
    },
    disableForm: function () {
      document.querySelector("input[type='submit']").disabled = true;
    },
    enableForm: function () {
      document.querySelector("input[type='submit']").disabled = false;
    },
    checkUploaderPattern: function () {
      let regex = /^[a-z0-9]{4}$/i;
      if (regex.exec(this.username)) {
        this.enableForm();
      } else {
        this.disableForm();
      }
    },
    checkAdministratorPattern: function () {
      this.email === this.username ? this.enableForm() : this.disableForm();
    },
  },
  watch: {
    username: function () {
      if (this.entity === "uploader") {
        this.checkUploaderPattern();
      } else {
        this.checkAdministratorPattern();
        console.log("administrator pattern check"+ this.checkAdministratorPattern());
      }
    },
    email: function(){
      if(this.entity==='administrator'){
        this.checkAdministratorPattern();
      }
    }
  },
};
</script>

<style scoped>
form {
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  width: 460px;
  margin-top: 5vh;
  margin-left: auto;
  margin-right: auto;
  font-family: "Nunito";
}

form > label[for="usernameField"],
form > label[for="emailField"],
form > label[for="nameSurnameField"],
form > label[for="passwordField"] {
  flex-basis: 30%;
  height: 4vh;
  margin: 3vh 0;
}

form > input[type="text"],
form > input[type="email"],
form > input[type="password"] {
  box-sizing: border-box;
  flex-basis: 70%;
  border-top: 0;
  border-right: 0;
  border-left: 0;
  border-bottom: 1px solid black;
  border-radius: 5px;
  background-color: #f9f9f9;
  font-family: "Baloo 2";
  height: 5vh;
  margin: 2vh 0;
}

form > span {
  flex-basis: 30%;
  margin: 2vh 0;
  height: 5vh;
}

form > label[for="radioUploader"],
form > label[for="radioAdministrator"] {
  flex-basis: 35%;
  text-align: center;
  margin: 2vh 0;
  height: 5vh;
}

form > input[type="submit"] {
  flex-basis: 80%;
  height: 8vh;
  border: 4px solid black;
  border-radius: 10px;
  color: rgb(214, 139, 0);
  background-color: rgba(0, 0, 0, 0.83);
  font-weight: bold;
  font-size: 5vh;
  font-family: "Russo One";
}

form > input[type="submit"]:hover {
  cursor: pointer;
}

form > input[type="submit"]:disabled {
  background-color: rgb(206, 206, 206);
  color: rgb(160, 160, 160);
  border: 1px solid grey;
  cursor: not-allowed;
  font-weight: lighter;
}

input:focus {
  outline: 0;
}

.disabled {
  border: 1px solid red;
}
</style>